import React from 'react';

import NavBar from './NavBar';

export default({searchAItem}) => (
  <header className="header-area">
    <div className="header">
      <NavBar />
      <div className="intro">
        <h1> </h1>
        <input onChange={(e) => searchAItem(e.target.value)} placeholder="   ?" />
        <p>           </p>
      </div>
    </div>
  </header>
)



